@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  h1 {
    @apply text-5xl font-bold text-center;
  }
}

@layer components {
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
    'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/** Custom gradient */
.gradient {
  /* 뭐.. 필요한거 없나 */
}
.gradient-cover {
  position: relative;
  width: 100%;
  height: 50px;
}
.gradient-cover p {
  position: absolute;
  right: 0;
  background-image: linear-gradient(140deg, #18181b 20%, #9c9c9c);
  opacity: 1;
  transition: opacity 0.5s linear;
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
}
.dark .gradient-cover p {
  background-image: linear-gradient(140deg, #fff 20%, #9c9c9c);
}

/* .dark .gradient-cover p {
  background-image: linear-gradient(140deg, #fff 20%, #9c9c9c);
} */
/* 아래 두 개는 layer components로 이동 */
.gradient-cover:first-child p::before {
  content: 'React To Do List';
}
.gradient-cover:nth-child(2) p::before {
  content: attr(data-before);
}

.gradient-cover p::before {
  position: absolute;
  background: linear-gradient(to right, #9c9c9c, #18181b);
  transition: opacity 0.5s linear;
  opacity: 0;
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
}

.dark .gradient-cover p::before {
  background: linear-gradient(to right, #9c9c9c, #fff);
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
}
.gradient .transitionOn::before {
  opacity: 1;
}
